<template>
    <Layout>
        <div class="card">
            <div class="card-body">
                <header class="mb-4">
                    <Search :types="search_types" @onSearch="onSearch($event)" />
                    <h5><i class="fa fa-list-alt"></i> </h5>
                </header>

                 <table class="table">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th></th>
                            <th>-</th>
                            <th>-</th>
                            <th></th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="item in bookings.result" :key="item.bk_id" :class="getApplyStatusClass(item)">
                            <td>{{ item.bk_id }}</td>
                            <td>{{ item.bk_title }}</td>
                            <td>{{ item.bk_time_start }}</td>
                            <td>{{ item.bk_time_end }}</td>
                            <td class="btns">
                                <div v-if="item.bk_status === 'pending'">
                                    <button @click="onUpdateStatus('allowed', item)" class="btn btn-sm btn-warning mr-2">
                                        <i class="fa fa-check-circle"></i> 
                                    </button>

                                    <button @click="onUpdateStatus('not allowed', item)" class="btn btn-sm btn-danger">
                                        <i class="fa fa-times-circle"></i> 
                                    </button>
                                </div>

                                <div v-if="item.bk_status === 'allowed'">
                                    <i class="fa fa-check-circle"></i> 
                                </div>

                                <div v-if="item.bk_status === 'not allowed'">
                                    <i class="fa fa-times-circle"></i> 

                                    <button @click="onDelete(item)" class="btn btn-sm btn-danger ml-2">
                                        <i class="fa fa-trash"></i> 
                                    </button>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                 </table>
                 <Pagination :data="bookings" :page="page" @onPage="onPage($event)" />
            </div>
        </div>
    </Layout>
</template>

<script>
import Layout from "@/components/Layout";
import Search from "@/components/Search";
import Pagination from "@/components/Pagination";
import { mapState } from "vuex";
import Axios from "axios";
export default {
  components: {
    Layout,
    Search,
    Pagination
  },
  computed: {
    ...mapState(["bookings"])
  },
  data() {
    return {
      search_types: [
        { name: "", value: "bk_title" },
        { name: "", value: "bk_time_start", type: "date" },
        { name: "", value: "bk_time_end", type: "date" }
      ],
      page: 1,
      search: {}
    };
  },
  mounted() {
    this.initialLoadBooking();
  },
  methods: {
    // 
    onDelete(item) {
      this.alertify.confirm("?", () => {
        Axios.delete(`/api/booking/manage/${item.bk_id}`)
          .then(response => this.initialLoadBooking())
          .catch(error => this.alertify.error(error.response.data.message));
      });
    },
    // 
    onUpdateStatus(bk_status, item) {
      this.alertify.confirm("?", () => {
        Axios.put(`/api/booking/manage/${item.bk_id}`, { bk_status })
          .then(response => this.initialLoadBooking())
          .catch(error => this.alertify.error(error.response.data.message));
      });
    },
    //    class
    getApplyStatusClass(item) {
      const statusClass = {};
      statusClass[item.bk_status] = true;
      return statusClass;
    },
    // 
    onSearch(search) {
      this.search = search;
      this.$store.dispatch("set_bookings", { page: 1, ...this.search });
    },
    // 
    onPage(page) {
      this.page = page;
      this.$store.dispatch("set_bookings", {
        page: this.page,
        ...this.search
      });
    },
    // 
    initialLoadBooking() {
      this.$store.dispatch("set_bookings");
    }
  }
};
</script>


<style scoped>
.btns {
  width: 200px;
  text-align: right;
}
.btns .btn {
  width: 82px;
}

tr.allowed td {
  color: #28a745;
}

tr.not.allowed td {
  color: #dc3545;
}
</style>

